import React  from 'react';
import { Container,  Card } from 'react-bootstrap';

export default (comp)=> {
    const bodyBox = (comp)=> centrelBox(<Container fluid={true} className="p-0 m-0 pb-3 text-left clearfix border border-warning rounded" 
        style={{minHeight:'36rem'}}>
            {comp}
        </Container>)

    const centrelBox = (comp)=> <Container fluid={true} className="p-0 pl-3 pr-3">
        <Container fluid={true} className="p-0 pl-3 pr-3  d-none d-xl-block">
        <Container fluid={true} className="p-0 pl-3 pr-3">
        <Container fluid={true} className="p-0 pl-3 pr-3">
            <Container fluid={true} className="p-0 pl-3 pr-3">
                {comp}
            </Container>
        </Container>
        </Container>
        </Container>

        <Container fluid={true} className="p-0 d-xl-none">
            {comp}
        </Container>
    </Container>

    const headerBox = (comp)=> <Container fluid={true} className="p-0 text-left clearfix text-light header-bg" style={{minHeight:'9.2rem'}}>
            {centrelBox(comp)}
        </Container>
    
    const cardShow = (v, k)=> <Container key={k} className="p-3 m-0 float-left text-center" style={{ width: '25%', minWidth:'20rem'}}>
    <Card border={v.style}  className={`rounded-0 bg-${v.style} hover-shadow p-0 pl-1`} 
        style={{ width:'100%', minWidth:'18rem', height:'13rem' }}>
        <Card.Body className="bg-light p-3">
            <Card.Title>{v.title}</Card.Title>
            <Card.Subtitle className="mb-2 text-secondary" style={{fontSize:'0.9rem'}}>{v.subtitle}</Card.Subtitle>
            <Card.Text style={{height:'5.2rem'}}>{v.description}</Card.Text>
            {!v.links ? '' : v.links.map(
                (v,k)=>  <Card.Link key={k} className="mr-3" href={v.caption ? `#${v.link}` : '#'}>{v.caption}</Card.Link>
            )}
        </Card.Body>
    </Card>
    </Container>

    
    const cardListShow = (cards)=> cards.map((v,k)=> cardShow(v, k));

  return { centrelBox, cardListShow, cardShow, headerBox, bodyBox }
}