import Web3 from 'web3';

export default (contractType, network)=> {
    
    const _provider = !Web3 || !Web3.givenProvider ? network : Web3.givenProvider;
    const isUpdateable = !Web3 || !Web3.givenProvider ? false : true;

    const createPostoffice = async (contractAddr, _name, callback)=>{
        const abi = require(`./abis/${contractType}`).default;
        const web3 = new Web3(Web3.givenProvider);

        const accounts = await web3.eth.getAccounts();

        const contractobj = new web3.eth.Contract(abi,contractAddr);
        // .investHub
        const q = await contractobj.methods.investHub(_name);
        const estGas = await q.estimateGas({
            from:accounts[0],
            to: contractAddr,
            value:web3.utils.toWei('1', 'finney')
        });
        const p = await q.send({
            from:accounts[0],
            to: contractAddr,
            value:web3.utils.toWei('1', 'finney'),
            gas: Math.round(estGas * 1.05)
        });
        const addedAddr = await contractobj.methods.getLastHub().call();
        const abiHub = require(`./abis/suezHub`).default;
        const addedContract = new web3.eth.Contract(abiHub,addedAddr);
        const addedname = await addedContract.methods.name().call();
        callback({
            address :  addedAddr,
            name    :   addedname
        });
    }

    const getUNSTInfo = async (contractAddr, callback) => {
        const abi = require(`./abis/${contractType}`).default;
        const web3 = new Web3(_provider);
        const contractobj = new web3.eth.Contract(abi,contractAddr);
        const info = await contractobj.methods.getUNSTinfo().call();
        callback(info);
    }

    const askPrice = async (contractAddr, callback) => {
        const abi = require(`./abis/${contractType}`).default;
        const web3 = new Web3(Web3.givenProvider);
        const contractobj = new web3.eth.Contract(abi,contractAddr);
        const q = await contractobj.methods.askPrice().call();
        callback(web3.utils.fromWei(q,'ether'));
    }

    return  { createPostoffice, getUNSTInfo, askPrice }

}