import React, { useState, useEffect }  from 'react';
import { Image } from 'react-bootstrap';
import { Engine} from '../comm';

export default ()=> {
    const eng = new Engine();
    const [ images, setImages ] = useState({});
    const [ tm, setTime ] = useState({});
    useEffect(()=>{
        eng.get('/metaData/images.json', v=>{
            console.log(v);
            setImages(v);
            setTime(new Date().getTime());
        })
    }, []);
    const showOrigin = (v, style)=>  !images[v]? '' : <Image src={images[v].link} style={style} />;

  return { showOrigin }
}