import React, {  useEffect, useState }  from 'react';
import { Container } from 'react-bootstrap';
import { Banner, Header, PostOfficeCreate  } from '../modules';
import { useUIComponents  } from '../customHooks';
import { Engine  } from '../comm';

import { Switch, Route } from "react-router-dom";
/*
import { Home, Banner, Header, Body, WalletAccount, TokenDetails,  TokensList, TrackFrom, MintFrom, NoExist, ContractAdmin  } from '../modules';
import { useWeb3Log, useFrameContainer } from "../customHooks";
import ContractProjects from './ContractProjects';
*/

export default (props)=> {
  const eng = new Engine();

  const { centrelBox, headerBox, cardListShow, bodyBox } = useUIComponents();
  const [ prodList , setProdList ] = useState([]);
  
  const loadProducts = ()=>{
    eng.get('/metaData/products.json', v=>{
      setProdList(v);
    });
  }
  useEffect(()=>{
    loadProducts();
  }, [])

  return (
    <Container fluid={true}  className="p-0 text-left  bg-inverse" style={{minHeight:'58rem'}}>
      {centrelBox(<Banner/>)}
      {headerBox(<Header/>)}
      <Container fluid={true} className="p-0 pt-2 text-center text-secondary home-bg" style={{minHeight:'36rem'}}>
          <Switch>
      `      <Route exact path={[
                  '/'
                  ]}>
                  {bodyBox(cardListShow(prodList))}
            </Route>
      `     <Route exact path={[ '/postOffice/create', '/postOffice' ]}>
                {bodyBox(<PostOfficeCreate comp="test"/>)}
            </Route>
            <Route path="/:title">
              <Container fluid={true} className="p-3 bg-light" >
              </Container>
          </Route>
          </Switch>
      </Container>
    </Container>
  );
}